import React from 'react';
import ListClusters from './list-cluster';
import { connect } from 'react-redux';
import { store } from '../../store/store';
import env from "../config/env";
import axios from "axios";


import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';


function mapSteToProps(state) {
    return { userAcceso: state.userAcceso }
}

class FormCluster extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            datos: { data: [] },
            error: {},
            id: '',
            nombre: '',
            activo: false,
            canSubmit: true,
            action: true,
        }
    }

    componentDidMount() {
        this.getData(1);
    }


    getError(response) {
        switch (response.status) {
            case 401:
                store.dispatch({
                    type: 'SET_USER_ACCESO',
                    payload: {
                        userAcceso: ''
                    }
                })
                break;
            case 422:
                this.setState({
                    error: response.data
                })
                break;
            case 404:
                console.log("error 404, archivo no encontrado")
                break;
            default:
                console.log('error inesperado')
                break;
        }
    }

    syncField(ev, fieldName) { // data form
        let element = ev.target;
        let value = element.value;
        let jsonState = {};
        jsonState[fieldName] = value;
        this.setState(jsonState);
    }

    async getData(page) {
        try {
            let res = await axios({
                url: env.endpoint + '/admin/clusters?page=' + page,
                method: 'get',
                timeout: 8000,
                headers: {
                    'Accept': '*/*',
                    'Authorization': this.props.userAcceso
                }
            })
            if (res.status === 200) {
                // peticion correctamente
                this.setState({ datos: res.data });

            }
        }
        catch (error) {
            this.getError(error.response);
        }
    }

    modificar = (id) => {
        this.getperiodo(id)
    }

    listData() {
        if (this.state.datos.data.length !== 0) {
            return <ListClusters handlePageChange={this.handlePageChange} datos={this.state.datos} modificar={this.modificar} activePage={this.state.activePage} action={true} />;
        } else {
            return "";
        }
    }
    handlePageChange = (pageNumber) => {
        this.setState({ activePage: pageNumber });
        this.getData(pageNumber);
    }



    updateStado = () => {
        this.setState({
            activo: !this.state.activo,
        });
    }

    modificar = (id) => {
        this.getcluster(id)
      }
      async getcluster(id) { // Obtener los datos para modificar 
        try {
          let res = await axios({
            url: env.endpoint + '/clusters/' + id,
            method: 'get',
            timeout: 8000,
            headers: {
              'Accept': '*/*',
              'Authorization': this.props.userAcceso
            }
          })
          if (res.status === 200) {
            this.setState({
              action: false,
              id: res.data.id,
              nombre: res.data.nombre,
              activo: res.data.activo,
            })
          }
        } catch (error) {
          this.getError(error.response);
        }
      }



      onSubmit = (e) => {
        this.setState({ canSubmit: false });
        axios({
          method: 'post',
          url: env.endpoint + '/clusters',
          data: {
            nombre: this.state.nombre,
            activo: this.state.activo
          }, headers: {
            'Accept': '*/*',
            'Authorization': this.props.userAcceso
          }
        }).then(response => {
          if (response.status === 203) {
            // peticion correctamente
            this.setState({
              Mensaje: true,
              error: { activo: response.data.Error },
              DatosPresupuesto: response.data,
              canSubmit: true
            })
            
          } else {
            this.clear();
            this.getData(1);
            this.setState({ activePage: 1 });
            this.setState({ canSubmit: true });
          }
        })
          .catch(error => {
            this.setState({ canSubmit: true });
            this.getError(error.response);
          });
    
      }
      onSubmitEditar = (e) => {
        this.setState({ canSubmit: false });
        axios({
          method: 'put',
          url: env.endpoint + '/clusters/' + this.state.id,
          data: {
            nombre: this.state.nombre,
            descripcion: this.state.descripcion, 
            fechaInicio: this.state.fechaInicio,
            fechaFin: this.state.fechaFin,
            activo: this.state.activo
          }, headers: {
            'Accept': '*/*',
            'Authorization': this.props.userAcceso
          }
        }).then(response => {
          if (response.status === 203) {
            // peticion correctamente
            this.setState({
              Mensaje: true,
              error: { activo: response.data.Error },
              DatosPresupuesto: response.data,
              canSubmit: true
            })
            
          } else {
            this.clear();
            this.getData(1);
            this.setState({ activePage: 1 });
            this.setState({ canSubmit: true });
          }
        })
          .catch(error => {
            this.setState({ canSubmit: true });
            this.getError(error.response);
          });
    
      }
      clear () {
        this.setState({
            action: true, 
            id: '',
            nombre: '',
            activo: false,
            error: {},
        })
      }


    render() {
        return (
            <div>
                 <h3>Admin Clusters</h3>
                <Grid container spacing={24}>
               
                    <Grid item xs={12} sm={6}>
                        <TextField
                            error={this.state.error.nombre ? true : false}
                            label={this.state.error.nombre ? this.state.error.nombre[0] : "Nombre"}
                            placeholder="Nombre "
                            value={this.state.nombre}
                            onChange={(e) => this.syncField(e, 'nombre')}
                            fullWidth={true}
                        />
                    </Grid>

                    <Grid item xs={12} sm={3}>

                        <InputLabel htmlFor="estado">{this.state.error.activo ? this.state.error.activo[0] : "Estado "}</InputLabel>
                        <FormControlLabel
                            control={
                                <Switch
                                    checked={this.state.activo}
                                    onChange={(e) => this.updateStado(e, 'rta')}
                                    value='activo'
                                />
                            }
                        />
                    </Grid>
                </Grid>
                {this.state.action ?
                    <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmit}> Registrar  </Button> :
                    <Button variant="contained" type="submit" disabled={!this.state.canSubmit} color="primary" onClick={this.onSubmitEditar}> Modificar  </Button>
                }

                {this.listData()}
            </div>
        );

    }



}

export default connect(mapSteToProps)(FormCluster);